// Rem output with px fallback
@mixin font-size($sizeValue: 1) {
	font-size: ($sizeValue * 16) * 1px;
	font-size: $sizeValue * 1rem;
}

// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}



// Column width with margin
@mixin column-width($numberColumns: 3) {
	width: map-get( $columns, $numberColumns ) - ( ( $columns__margin * ( $numberColumns - 1 ) ) / $numberColumns );
}


// media queries
@mixin query-phone-up {
  @media (min-width: 34em) { @content; }
}
@mixin query-phone-down {
  @media (max-width: 34em) { @content; }
}
@mixin query-tablet-up {
  @media (min-width: 48em) { @content; }
}
@mixin query-tablet-down {
  @media (max-width: 48em) { @content; }
}
@mixin query-desktop-up {
  @media (min-width: 62em) { @content; }
}
@mixin query-big-desktop-up {
  @media (min-width: 75em) { @content; }
}
