/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
optgroup,
textarea {
	color: #404040;
	font-family: "Lato", sans-serif;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.8em;
}

h1, h2, h3, h4, h5, h6 {
	font-family: "Lato", sans-serif;
	margin: 0 0 0.1em;
	line-height: normal;
}

.single .hentry h1, .single .hentry h2, .single .hentry h3, .single .hentry h4, .single .hentry h5, .single .hentry h6, .page .hentry h1, .page .hentry h2, .page .hentry h3, .page .hentry h4, .page .hentry h5, .page .hentry h6 {
	margin: 1em 0 0.5em;
}

.single .hentry .entry-title, .page .hentry .entry-title {
	margin: 0 0 0.1em;
}

h1 {
	font-size: 2.2em;
}

h2 {
	font-size: 1.6em;
}

h3 {
	font-size: 1.4em;
}

h4 {
	font-size: 1.2em;
}

h5 {
	font-size: 1em;
}

h6 {
	font-size: 0.8em;
}

p {
	margin: 0  0 1.6em 0;
}

.archive p, .blog p {
	margin: 0  0 0.5em 0;
}

aside p {
	margin: 0;
}

dfn, cite, em, i {
	font-style: italic;
}

blockquote {
	margin: 0 1.5em;
}

address {
	margin: 0 0 1.5em;
}

pre {
	background: #eee;
	font-family: "Courier 10 Pitch", Courier, monospace;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1.6;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 15px;
	font-size: 0.9375rem;
}

abbr, acronym {
	border-bottom: 1px dotted #666;
	cursor: help;
}

mark, ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 125%;
}

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;
}

body {
	background: #fff;
	/* Fallback for when there is no custom background color defined. */
}

hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

ul, ol {
	margin: 0;
	padding: 0;
}

ul {
	list-style: none;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin: 0;
	padding: 0;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

.style-list {
	margin-left: 1.5em;
	padding: 0;
	list-style: disc;
}

img {
	height: auto;
	/* Make sure images are scaled correctly. */
	max-width: 100%;
	/* Adhere to container width. */
}

figure {
	margin: 1em 0;
	/* Extra wide images within figure tags don't overflow the content area. */
}

.archive figure, .blog figure {
	margin: 0;
	/* Extra wide images within figure tags don't overflow the content area. */
}

table {
	margin: 0;
	width: 100%;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Links
--------------------------------------------------------------*/
a {
	color: black;
	text-decoration: none;
}

a:visited {
	color: black;
}

a:hover, a:focus, a:active {
	color: #06738E;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
	text-decoration: underline;
}

.entry-content a {
	color: #08a5cb;
}

.entry-content a:visited {
	color: #08a5cb;
}

.entry-content a:hover, .entry-content a:focus, .entry-content a:active {
	color: #06738E;
}

.breadcrumbs a {
	color: #8dbfcb;
}

.breadcrumbs a:visited {
	color: #8dbfcb;
}

.breadcrumbs a:hover, .breadcrumbs a:focus, .breadcrumbs a:active {
	color: #06738E;
}

.entry-meta a {
	color: #08a5cb;
}

.entry-meta a:visited {
	color: #08a5cb;
}

.entry-meta a:hover, .entry-meta a:focus, .entry-meta a:active {
	color: #06738E;
}

.tags-links a {
	color: #8dbfcb;
}

.tags-links a:visited {
	color: #8dbfcb;
}

.tags-links a:hover, .tags-links a:focus, .tags-links a:active {
	color: #06738E;
}

h1 a:hover, h2 a:hover, h3 a:hover, h4 a:hover, h5 a:hover, h6 a:hover {
	text-decoration: none;
}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
}

.main-navigation ul ul {
	display: none;
	box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
	float: left;
	position: absolute;
	top: 100%;
	left: 0;
	z-index: 99999;
	background-color: white;
	padding: 10px;
	min-width: 300px;
	justify-content: flex-start;
}

.main-navigation ul ul ul {
	left: -999em;
}

.main-navigation ul .children li {
	margin: 5px 0;
}

.main-navigation a {
	text-decoration: none;
}

/* Small menu. */
.menu-item-has-children,
.page_item_has_children {
	position: relative;
}

ul.children.toggled-on {
	left: 1em;
}

.main-navigation {
	position: relative;
}

.menu-toggle {
	display: block;
	position: absolute;
	top: -3em;
	right: 0;
}

.menu-toggle, .toggled-on ul,
.sub-menu.toggled-on {
	display: block;
}

.sub-menu.toggled-on {
	display: block;
	left: 0;
}

@media (min-width: 48em) {
	.menu-toggle {
		display: none;
	}
