		flex-wrap: wrap;
		font-size: 0.9em;
		justify-content: flex-end;
	}
	.main-navigation ul li a {
		padding: 0 0.6em;
	}
	.main-navigation ul li:last-child a {
		padding: 0 0 0 1em;
	}
}

@media (min-width: 62em) {
